import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "../Home/Home";
import AddWords from '../AddWords/AddWords';
import RandomTen from '../RandomTen/RandomTen';

function App() {
  
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path="/english_memory" component={Home} />
          <Route path="/addWords" component={AddWords} />
          <Route path="/randomTen" component={RandomTen} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
